@import url('https://fonts.googleapis.com/css2?family=Raleway:wght@300&display=swap');


.principal-container{
    width: 100%;
    margin: 0 auto;
    display: flex;
    flex-direction: column;
    padding-top: 25px;
    text-align: center;
}

.fas{
    color: #fff;
    font-size: 100px;
}

.principal-title{
    font-family: 'Raleway', sans-serif;
    font-size: 50px;
    color: #fff;
}

.principal-text{
    font-family: 'Raleway', sans-serif;
    font-size: 25px;
    color: #fff;
    margin-bottom: 40px;
}

.form-container{
    background-color: #fff;
    width: 50%;
    margin: 0 auto;
    margin-bottom: 15px;
    padding-top: 30px;
    padding-bottom: 30px;
    border-radius: 5px;
    display: flex;
    flex-direction: column;
}

.number-shows{
    width: 50%;
    height: 125px;
    margin: 0 auto;
    margin-bottom: 15px;
    font-family: 'Raleway', sans-serif;
    font-size: 100px;
}

.input-form{
    width: 20%;
    margin: 0 auto;
    padding: 10px;
    font-family: 'Raleway', sans-serif;
    font-size: 25px;
    text-align: center;
    margin-bottom: 15px;
}

.btn-form{
    width: 30%;
    font-family: 'Raleway', sans-serif;
    margin: 0 auto;
    margin-bottom: 40px;
    border: none;
    color: #fff;
    padding: 10px;
}

#textDataMath{
    width: 90%;
    height: 100px;
    margin: 0 auto;
    font-family: 'Raleway', sans-serif;
    font-size: 25px;
}

.creator{
    font-family: 'Raleway', sans-serif;
    color: #fff;
    margin-bottom: 25px;
}

/*=====================Media query for phones=====================*/

@media (max-width:600px){

.principal-container{
    height: 100vh;
}

.principal-title{
    font-size: 40px;
}
    
.principal-text{
    font-size: 20px;
}

.form-container{
    width: 80%;
}

.btn-form{
    width: 40%;
}

#textDataMath{
    font-size: 20px;
}

}/*End of media query for phones*/

/*=====================Media query for tablets=====================*/

@media (min-width:768px){

.principal-container{
    height: 100vh;
}

.principal-title{
}

.form-container{
    width: 60%;
}
                
}/*End of media query for tablets*/

/*=====================Media query for laptops=====================*/

@media (min-width:992px){

.principal-title{
    font-size: 50px;
}

.principal-text{
    font-size: 30px;
}

.form-container{
    width: 50%;
}
            
}/*End of media query for laptops*/